.locations-list-row{
  justify-content: center;
}

#locations-list{
  background-color: #F5F5F5;
  flex-direction:row;
  flex-wrap: wrap;
}

.location-title{
  font-size: 14px;
}

.location-website{
  display: block;
}

.location-form-modal{
    width: 50vw;
    max-width: 50vw;
}

.location-form-modal .modal-content {
}

.login-modal{
  width: 30vw;
  max-width: 30vw;
}

@media (max-width: 1024px) {
  .logo{
    height:150px;
    width: 375px;
    margin-top: 75px;
  }
}

@media (max-width: 796px) {
  .location-form-modal{
      width: 90vw;
      max-width: 90vw;
  }
  .login-modal{
      width: 75vw;
      max-width: 75vw;
  }

  .banner{
    height: 200px;
    text-align: center;
  }

  .logo{
    height:100px;
    width: 250px;
    margin-top: 50px;
  }

  .nav-phone{
    display: inline;
  }

  .nav-full{
    display: none;
  }
}

@media (max-width: 600px) {
  .login-modal{
      width: 90vw;
      max-width: 90vw;
  }
}


.days-open{
  margin-top: 30px;
  margin-bottom: 0px;
}

.hours-open{
  margin-top: 10px;
  margin-bottom: 0px;
}

#open-info-lable{
  margin-left: 20px;
  margin-right: 10px;
}

.needs-row{
  justify-content: center;
}

.event-preview{
  color:#fff;
}

.event-preview img{
  width: 100%;
  height:100%;
}

.crop-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 80px;
}

.add-event{
  justify-content: center;
  align-items: center;
  background-color: gray;
}

.event-plus-icon{
  font-size: 100px;
  color: #fff;
}


.employee-page-row{
  justify-content: space-between;
}

/* Home Page */
.home-body{
  justify-content: center;
  margin-top: 30px;
  font-family: 'Cormorant Garamond';
  margin-left: 15px;
  margin-right: 15px;

}

.mission-statement{

}

.mission-statement h1{
  color: #273D63;
  font-size: 50px;
  font-weight: bold;
}

.mission-statement ol{
  background-color: #B4C5D5;
  color: #273D63;
  font-weight: bold;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 18px;
  padding-right: 15px;
}

/* Current Needs Page */

.current-needs-body{
  justify-content: center;
  margin-top: 40px;
  font-family: 'Cormorant Garamond';
  margin-left: 15px;
  margin-right: 15px;

}

.current-needs-page-name{
  font-size: 44px;
  text-align: center;
  margin-bottom: 40px;
}

.current-needs-don-app{
  font-size: 30px;
  text-align: center;
  margin-bottom: 30px;
}

.current-needs-page-para{
  font-size: 22px;
  line-height: 2;
  margin-bottom: 30px;
}

.current-needs-body ul{
  padding: 5px;
  margin: 0;
}

.amazon-button{
  background-color: #F08A3A
}

/* Get Help */

.get-help-body{
  margin-bottom: 100px;
}


.bm-burger-button {
  position: relative;
  width: 30px;
  height: 25px;
  right: 15px;
  float: right;
}


.bm-burger-bars {
  background-color: black;
}


.bm-burger-bars-hover {
  background: blue;
}


.bm-cross-button {
  height: 24px;
  width: 24px;
}


.bm-cross {
  background: #bdc3c7;
}


.bm-menu-wrap {
  position: relative;
  height: 100%;
}


.bm-menu {
  background: #fff;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}


.bm-morph-shape {
  fill: #fff;
}


.bm-item-list {
  color: blue;

}


.bm-item {
  display: inline-block;
}
